  <lay-header :showBack="true" :tBarColor="'#FFFFFF'" :txtTitle="'1:1문의'">
    <v-layout column class="pt-4 px-5 pb-6 lay-body">
      <span class="txt-desc pb-6"
        >제품의 파손이나 불량의 경우 사진과 함께 구체적인 내용을 남겨주세요.
        상품과 무관한 내용이거나 불법적인 내용은 통보없이 삭제될 수 있습니다.<br /><br />
        산업안전보건법에 따라 고객응대근로자 보호조치를 시행하고 있습니다. 욕설,
        성희롱, 폭언, 신체적 정신적 고통을 유발하는 행위가 발생할 경우 답변이
        어려울 수 있습니다.</span
      >
      <span class="txt-sub mt-10">무엇에 대한 문의인가요?</span>
      <c-input
        :hint="inputList.type.hint"
        :value.sync="inputList.type.value"
        readonly
        @click.native.stop="() => onShowPopupAction(bPop)"
      >
        <img
          src="@/assets/images/ic-arrow-r-b-16.svg"
          class="rotate90"
          slot="append"
        />
      </c-input>
      <span class="txt-sub mt-10">상세 내용을 작성해 주세요.</span>
      <c-input
        :hint="inputList.title.hint"
        :value.sync="inputList.title.value"
      />
      <v-textarea
        v-model="inputList.cont.value"
        auto-grow
        dense
        flat
        rows="1"
        hide-details
        no-resize
        loader-height="0"
        :placeholder="inputList.cont.hint"
        maxlength="5000"
        class="mt-4 flex-grow-0"
        color="rgba(21, 212, 224, 0.3)"
      />
      <span class="align-self-end mt-2 txt-cnt"
        ><span>{{
          inputList.cont.value ? inputList.cont.value.length : 0
        }}</span
        >/5000</span
      >
      <v-layout class="flex-grow-0">
        <img
          v-ripple
          src="@/assets/images/ic-attach-24.svg"
          class="ic-attach"
          @click.prevent.stop="onClickActionFile"
        />
        <v-layout row wrap class="lay-attach ml-2">
          <v-img
            v-for="(item, index) in uploadFiles"
            :key="index"
            src="@/assets/images/img-main-bg.png"
            class="img-attach"
            aspect-ratio="1"
            :max-width="pxToRem(64)"
          >
            <img src="@/assets/images/ic-circle-x.svg" class="ic-x" />
            <span class="txt-duration">00:52</span>
          </v-img>
        </v-layout>
      </v-layout>
      <v-file-input
        v-model="files"
        ref="uploader"
        v-show="false"
        :multiple="false"
        @change="inputFileChange"
      />
      <span class="txt-file-sub mt-3 mb-2">
        파일에 개인정보가 포함되지 않도록 주의해 주세요.<br />
        사진은 5개까지, 동영상은 1분 내 길이로 2개까지 등록 가능합니다.
      </span>
      <span class="txt-sub mt-10"
        >다이버님의 이름과 연락 가능한 전화번호를 입력하세요.</span
