<script>
import cListItem from "@/components/cListItem.vue";
import PSelect from "../../popup/pSelect.vue";
import CFooter from "@/components/cFooter.vue";
export default {
  components: { cListItem, PSelect, CFooter },
  data() {
    return {
      files: [],
      uploadFiles: [],
      typeIndex: null,
      emailCheck: false,
      inputList: {
        type: {
          value: null,
          hint: "유형을 선택하세요",
        },
        title: {
          value: null,
          hint: "제목을 입력하세요",
        },
        cont: {
          value: null,
          hint: "상세한 내용을 작성하세요",
        },
        name: {
          value: null,
          hint: "이름",
        },
        loc: {
          value: null,
          hint: "+82",
        },
        tel: {
          value: null,
          hint: "전화번호",
        },
      },
      bPop: {
        isCreate: false,
        isShow: false,
      },
      types: [
        {
          title: "다이브로이드 기기",
          ic: require("@/assets/images/ic-equipment-20.svg"),
          seq: "001",
        },
        {
          title: "액세서리",
          ic: require("@/assets/images/ic-accessary-20.svg"),
          seq: "002",
        },
        {
          title: "고장/수리/오작동",
          ic: require("@/assets/images/ic-brokedown-20.svg"),
          seq: "003",
        },
        {
          title: "앱 사용",
          ic: require("@/assets/images/ic-app-20.svg"),
          seq: "004",
        },
        {
          title: "계정 및 회원 정보",
          ic: require("@/assets/images/ic-my-20.svg"),
          seq: "005",
        },
        {
          title: "구매/반품/환불",
          ic: require("@/assets/images/ic-box-20.svg"),
          seq: "006",
        },
        {
          title: "배송",
          ic: require("@/assets/images/ic-delivery-20.svg"),
          seq: "007",
        },
        {
          title: "기타",
          ic: require("@/assets/images/ic-etc-20.svg"),
          seq: "008",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    onClickActionType() {},
    onClickActionFile() {
      if (this.uploadFiles.length < 5) {
        this.$refs.uploader.$refs.input.click();
      } else {
        this.showMsgPopup("첨부파일은 최대 5개까지 등록가능합니다.");
      }
    },
    inputFileChange(val) {
      val.forEach((file) => {
        if (this.uploadFiles.length > 5) {
          this.showMsgPopup("첨부파일은 최대 5개까지 등록가능합니다.");
          return;
        }
        // if (!this.uploadFiles.some((f) => f.name == file.name)) {
        //     this.uploadFiles.push(file);
        // }

        if (file.size > 10 * 1024 * 1024) {
          this.showMsgPopup("첨부파일은 최대 10MB의 용량제한이 있습니다.");
          return;
        }

        if (this.accept != "*/*") {
          var extName = file.name
            .substring(file.name.lastIndexOf(".") + 1)
            .toLowerCase();
          var str = this.$refs.uploader.$refs.input.accept
            .replace(/\./g, "")
            .replace(/(\s*)/g, "")
            .toLowerCase()
            .split(",");
          if (str.indexOf(extName) < 0) {
            this.showMsgPopup(
              "업로드 가능한 파일 유형은 " +
                this.$refs.uploader.$refs.input.accept
                  .replace(/\./g, "")
                  .toUpperCase() +
                " 입니다."
            );
            return;
          }
        }

        if (!this.uploadFiles.some((f) => f.oriFileName == file.name)) {
          let tFile = {
            oriFileName: file.name,
            fileSize: file.size,
            type: file.type.toLowerCase().startsWith("image")
              ? 1
              : file.type.toLowerCase().startsWith("video")
              ? 2
              : 3,
            file: file,
          };
          this.uploadFiles.push(tFile);
        }
      });
      // this.uploadFiles = this.uploadFiles.concat(val)
    },
    removeFile(val) {
      this.uploadFiles.splice(val, 1);
    },
  },
};
</script>

